 margin: 0 auto;\r\n}\r\n\r\nmain.blur{\r\n  filter: blur(4px);\r\n  transition: all 0.3s ease-in-out;\r\n}\r\n\r\n.hero_container{\r\n  position: relative;\r\n  height: 320px;\r\n\r\n  img{\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: -100;\r\n    width: 100%;\r\n    height: 100%;\r\n    object-fit: cover;\r\n    filter: brightness(0.55);\r\n    \r\n  }\r\n\r\n  .hero_content{\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    height: 100%;\r\n    color: white;\r\n    gap: 8px;\r\n    text-align: center;\r\n    \r\n    h1{\r\n      text-transform: uppercase;\r\n      font-size: 32px;\r\n    }\r\n    p{\r\n      width: 90%;\r\n      font-size: 14px;\r\n      line-height: 1.5rem;\r\n    }\r\n  }\r\n}\r\n\r\n.resto_container{\r\n  max-width: 1400px;\r\n  margin: 0 auto;\r\n  padding: 36px 20px;\r\n\r\n  .resto_title{\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    gap: 8px;\r\n    margin-bottom: 28px;\r\n\r\n    h2{\r\n      font-size: 24px;\r\n    }\r\n    p{\r\n      font-size: 14px;\r\n    }\r\n  }\r\n}\r\n\r\n.resto_list{\r\n  display: grid;\r\n  grid-template-columns: repeat(1, minmax(0, 1fr));\r\n  gap: 20px;   \r\n}\r\n\r\n.resto_item{\r\n  filter: drop-shadow(0 10px 8px rgb(0 0 0 / 0.04)) drop-shadow(0 4px 3px rgb(0 0 0 / 0.1));\r\n  background-color: white;\r\n  border-radius: 0.5rem;\r\n  overflow: hidden;\r\n  transition: all 0.2s ease-in-out;\r\n\r\n  &:hover{\r\n    transform: translate(0, -4px);\r\n  }\r\n\r\n  .resto_image_wrapper{\r\n    position: relative;\r\n    height: 200px;\r\n    overflow: hidden;\r\n\r\n    img{\r\n      width: 100%;\r\n      height: 100%;\r\n      object-fit: cover;\r\n      position: absolute;\r\n      bottom: 0;\r\n      left: 0;\r\n      z-index: -10;\r\n      border-radius: 0.5rem 0.5rem 0 0;\r\n      transition: all 0.3s ease-in-out;\r\n    }\r\n\r\n    &:hover img{\r\n      transform: s