n        font-size: 36px;\r\n      }\r\n      p{\r\n        font-size: 16px;\r\n        width: 70%;\r\n      }\r\n    }  \r\n  }\r\n  .resto_list{\r\n    grid-template-columns: repeat(2, minmax(0, 1fr));   \r\n  }\r\n  .resto_item{\r\n    .resto_content{\r\n      min-height: 395px;\r\n    }\r\n  }\r\n  .footer_container{\r\n    padding: 0 40px;\r\n  \r\n    img{\r\n      height: 50px;\r\n    }\r\n    p{\r\n      font-size: 16px;\r\n      font-weight: $medium;\r\n    }\r\n  }\r\n}\r\n\r\n//screen laptop\r\n@media screen and (min-width: 900px) {\r\n  #hamburger{\r\n    display: none;\r\n  }\r\n  .nav_container{\r\n    padding: 0 16px;\r\n    .nav_brand{\r\n      .logo_wrapper{\r\n        height: 44px;\r\n        padding: 0 28px;\r\n        display: inline-block;\r\n        \r\n        #logo{\r\n          height: 40px;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  .nav_content{\r\n    .nav_list{\r\n      display: flex;\r\n      flex-direction: row;\r\n      gap: 20px;\r\n      align-items: center;\r\n      justify-content: center;\r\n      height: 100%;\r\n      width: auto;\r\n      margin: 0;\r\n      background-color: transparent;\r\n      position: relative;\r\n      top: 0;\r\n      right: 0;\r\n      animation: none;\r\n      a{\r\n        padding: 0px 28px;\r\n      }\r\n    }\r\n  }\r\n\r\n  .hero_container{\r\n    height: 400px;\r\n    img{\r\n      min-width: 1000px;\r\n    }\r\n  \r\n    .hero_content{\r\n      h1{\r\n        font-size: 48px;\r\n      }\r\n      p{\r\n        font-size: 18px;\r\n        width: 100%;\r\n      }\r\n    }\r\n  }\r\n\r\n  .resto_container{\r\n    padding: 36px 40px;\r\n\r\n    .resto_title{\r\n      h2{\r\n        font-size: 28px;\r\n      }\r\n      p{\r\n        font-size: 16px;\r\n      }\r\n    }\r\n  }\r\n\r\n  .resto_list{\r\n    grid-template-columns: repeat(3, minmax(0, 1fr));\r\n    gap: 28px 20px;   \r\n  }\r\n  .resto_item{\r\n    .resto_content{\r\n      min-height: 335px;\r\n    }\r\n  }\r\n}"],"sourceRoot":""}]);
